import { WINDOW_RESIZE } from "../actions/other";

const initialState = {
  isKeyboardActive: false,
};

export function otherReducer(state = initialState, action) {
  const { type } = action;

  switch (type) {
    case WINDOW_RESIZE:
      return { ...state, isKeyboardActive: !state.isKeyboardActive };
    default:
      return state;
  }
}
